.button {
    border: none;
    outline: none;
    border-radius: 3px;
    padding: 5px 24px;
    min-width: 132px;
    height: 30px;
    font-weight: 600;
}

.disabled {
    background: rgba(0, 77, 153, 0.5);
    color: rgba(0, 46, 92, 1);
    opacity: 0.5;
}

.secondary {
    border: 1px solid rgba(0, 102, 204, 1);
    color: rgba(0, 102, 204, 1);
    background: white;
}

.default {
    background: rgba(0, 102, 204, 1);
    color: white;
}

.red {
    background: rgba(204, 20, 57, 1);
}

.default:hover {
    background: rgba(0, 128, 255, 1);
}

.red:hover {
    background: rgba(255, 26, 71, 1);
}

.secondary:hover {
    background: rgba(229, 240, 250, 1);
}


.default:active {
    background: rgba(0, 89, 178, 1);
}

.secondary:active {
    background: rgba(201, 229, 255, 1);
}

.red:active {
    background: rgba(178, 18, 50, 1);
}

.button:disabled {
}
